.switcher {
  vertical-align: middle;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: inline-block;
  position: relative;
}

.switcher .dark-button {
  box-sizing: border-box;
  width: 40px;
  height: 20px;
  vertical-align: top;
  background: #fff;
  border: 1px solid #fd5103;
  border-radius: 20px;
  transition: all .2s;
  display: inline-block;
  position: relative;
}

.switcher .dark-button:before {
  content: "";
  width: 16px;
  height: 16px;
  background: #fd5103;
  border-radius: 50%;
  transition: all .15s;
  display: inline-block;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switcher input[type="checkbox"] {
  width: 0;
  height: 0;
  z-index: -1;
  opacity: 0;
  display: block;
  position: absolute;
}

.switcher input[type="checkbox"]:checked + .dark-button {
  background: #fd5103;
}

.switcher input[type="checkbox"]:checked + .dark-button:before {
  background: #fcfcfc;
  transform: translateX(20px);
}

.switcher input[type="checkbox"]:not(:disabled) + .dark-button {
  cursor: pointer;
  border-color: #fd5103;
}

* {
  box-sizing: border-box;
